body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
}
/* navbar */
.nav-position{
  position: absolute;
  top: 0px;
  width: 100%;
  z-index: 2;
}


/* Navbar */

/* Header */
.bg-vidoe , .bg-video-cover{
  height: 100vh;
  width: 100%;
  background-color: rgba(41, 39, 39,0.6);
  border-bottom: 8px solid #f3c761;
}
.header-bottom-nav{
  box-shadow: 2px 4px solid gray;
}
.header-bottom-nav div{
  border-right: 1px solid #ececec;
  font-size: 16px;
  box-shadow:0 5px 5px 0 rgba(0,0,0,.25);
  height: 10vh;
  display: flex;
}
.image-bg, .image-bg-cover{
  height: 100%;
  font-weight: 700!important;
  text-transform: uppercase;
}

.image-bg{
  /* background-image: url('./image/pexels-photo-1166657.jpeg'); */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.image-bg:hover{
  background-color: transparent;
  background-image: none;
}
.image-bg:hover .image-bg-cover{
  background-color: transparent;
}

.image-bg-cover{
  background-color: rgba(102, 51, 153,0.7)
}
.small-side-navbar-section{
  padding: 6em 1em;
  /* background-color: rgba(102, 51, 153,0.008) */

}
.small-side-navbar  li{
  list-style-type: none!important;
  border-bottom: 1px solid rgb(241, 189, 189);
   
}
.header-bottom-nav div a, .small-side-navbar  a{
  text-decoration: none!important;
  font-weight: 900;
  font-size: 16;
  text-transform: uppercase;
  padding: 2em 1em;
  ;position: relative;
  justify-items: center
}
.hr{
  border-bottom:4px solid white;
}
.image-bg-link{
  text-decoration: none!important;
}
.small-side-navbar li {
    position: relative;
}

.bg-image80 , .bg-image-cover, .bg-image-cover video{
  height: 100vh!important;
  width: 100%!important;
}
.bg-image-cover{
  transition: all 7s ease-in-out 0s;
  -webkit-transition: all 500ms ease-in-out 0s;
}
.bg-image-cover:hover{
  background-color: rgba(16, 22, 26,0.4);
  transition: all 500ms ease-in-out 0s;
  -webkit-transition: all 500ms ease-in-out 0s;
}
.bg-image80{
  background-image: url(/school/static/media/code1.8f51b4d9.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.box-section{
  border: 1px solid rgb(209, 208, 208)!important;
  transition: 6s color;
}


.box-section-text h4{
  font-weight: 700;
  font-size: 21px;
  margin-bottom: 21px!important;
  color: #10161A;
}
.box-section-text p {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.box-section:hover:after{
 width: 100%;
}
.box-section:hover h4{
  color: #fff!important;
  transition: 0.2s width;
 }
.box-section::after {
  content: "";
  background-color: #1C1D21;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 0%;
  z-index: -1;
  transition: 0.2s width;
}
/* Header */

